html{
    height: 100%;
    width: 100%;
}
body {
    padding: 0;
    margin: 0;
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.50) 100%), url(/funbox-test-html/static/media/pattern.acfeac28.jpg);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.50) 100%), url(/funbox-test-html/static/media/pattern.acfeac28.jpg);
    color: white;
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    height: 100%;
    /* min-height: 100%; */
    
}
#root{
    
    min-height: 100%;
    /* box-sizing: border-box; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

@media(min-width: 1200px){
    #root{
        height:100%; /* vertical align center for IE */
    }
}


@font-face {
    font-family: exo2;
    src: url(/funbox-test-html/static/media/Exo2.0-Thin.d9d2ef1a.eot); /* IE9 Compat Modes */
    src: url(/funbox-test-html/static/media/Exo2.0-Thin.d9d2ef1a.eot?#iefix) format('embedded-opentype'), 
       url(/funbox-test-html/static/media/Exo2.0-Thin.3dc0b348.woff) format('woff'), 
       url(/funbox-test-html/static/media/Exo2.0-Thin.1e7bac79.ttf)  format('truetype'), /* Safari, Android, iOS */
}

h1{
    font-family: exo2;
    font-weight: normal;
    font-size: 36px;
    text-shadow: 0px 1px 0px black;
}

a, a:visited{
    color: #1698d9;
    text-decoration: none;
    border-bottom: dashed 1px #1698d9;
}
a:hover{
    color: #2ea8e6;
}



.app {
  width: 100%; }

.product-container {
  text-align: center;
  width: 320px;
  margin: 0 40px;
  display: inline-block; }
  @media (max-width: 430px) {
    .product-container {
      margin: 0; } }
  .product-container .product-border {
    width: 320px;
    height: 480px;
    background: -webkit-linear-gradient(315deg, transparent 30px, #1698d9 31px);
    background: linear-gradient(135deg, transparent 30px, #1698d9 31px);
    border-radius: 12px;
    position: relative;
    cursor: pointer; }
  .product-container.hover .product-border {
    background: -webkit-linear-gradient(315deg, transparent 30px, #1698d9 31px);
    background: linear-gradient(135deg, transparent 30px, #1698d9 31px); }
  .product-container.selected .product-border {
    background: -webkit-linear-gradient(315deg, transparent 30px, #d91667 31px);
    background: linear-gradient(135deg, transparent 30px, #d91667 31px); }
  .product-container.selected.hover .product-border {
    background: -webkit-linear-gradient(315deg, transparent 30px, #e62e7a 31px);
    background: linear-gradient(135deg, transparent 30px, #e62e7a 31px); }
  .product-container.disabled .product-border {
    background: -webkit-linear-gradient(315deg, transparent 30px, #b3b3b3 31px);
    background: linear-gradient(135deg, transparent 30px, #b3b3b3 31px); }
  .product-container .product {
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    background: -webkit-linear-gradient(315deg, transparent 28px, #f2f2f2 29px);
    background: linear-gradient(135deg, transparent 28px, #f2f2f2 29px);
    content: '';
    border-radius: 8px; }
    .product-container .product:before {
      content: '';
      position: absolute;
      top: 0px;
      left: 0px;
      right: 0px;
      bottom: 0px;
      background: url(/funbox-test-html/static/media/cat-small.25f881ab.png) -4px 203px no-repeat;
      border-radius: 8px; }
  .product-container.disabled .product:before {
    opacity: 0.5; }
  .product-container .product-above-header {
    position: absolute;
    color: #666666;
    font-size: 16px;
    top: 17px;
    left: 44px; }
  .product-container.hover.selected .product-above-header {
    color: #e62e7a; }
  .product-container.disabled .product-above-header {
    color: #b3b3b3;
    opacity: 0.5; }
  .product-container .product-header {
    position: absolute;
    color: black;
    font-size: 48px;
    font-weight: bold;
    top: 41px;
    left: 42px; }
  .product-container.disabled .product-header {
    color: #b3b3b3;
    opacity: 0.5; }
  .product-container .product-sub-header {
    position: absolute;
    color: black;
    font-size: 24px;
    font-weight: bold;
    top: 95px;
    left: 44px; }
  .product-container.disabled .product-sub-header {
    color: #b3b3b3;
    opacity: 0.5; }
  .product-container .product-text {
    position: absolute;
    color: #666666;
    font-size: 14px;
    top: 138px;
    left: 46px;
    white-space: pre-line;
    text-align: left; }
  .product-container.disabled .product-text {
    color: #b3b3b3;
    opacity: 0.5; }
  .product-container .product-weight {
    position: absolute;
    background: #1698d9;
    font-size: 21px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    bottom: 11px;
    right: 11px;
    text-align: center; }
    .product-container .product-weight .number {
      font-size: 42px;
      line-height: 30px;
      padding-top: 16px; }
    .product-container .product-weight .label {
      padding-top: 2px; }
  .product-container.hover .product-weight {
    background: #2ea8e6; }
  .product-container.selected .product-weight {
    background: #d91667; }
  .product-container.hover.selected .product-weight {
    background: #e62e7a; }
  .product-container.disabled .product-weight {
    background: #b3b3b3; }
  .product-container .product-sub-text {
    color: white;
    text-shadow: 0px 1px 0px black;
    font-size: 13px;
    padding-top: 14px;
    padding-bottom: 30px; }
  .product-container.disabled .product-sub-text {
    color: #ffff66; }

